.arabic-font {
  font-family: 'Amiri', 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}


.card-center {
  max-width: 65%;
  margin: 0 auto;
  float: none;
  margin-bottom: 10px;
}


.shadow-effect {
  -webkit-box-shadow: 4px 0px 15px 0px rgba(0,0,0,0.3);
  -moz-box-shadow: 4px 0px 15px 0px rgba(0,0,0,0.3);
  box-shadow: 4px 0px 15px 0px rgba(0,0,0,0.3);
}

.bg-light-grey {
  background-color: #f9f9f9;
}


/*Loader Effect*/
.lds-dual-ring {
  display: inline-block;
  width: 80px;
  height: 80px;
}
.lds-dual-ring:after {
  content: " ";
  display: block;
  width: 64px;
  height: 64px;
  margin: 8px;
  border-radius: 50%;
  border: 6px solid #000;
  border-color: #000 transparent #000 transparent;
  animation: lds-dual-ring 1.2s linear infinite;
}
@keyframes lds-dual-ring {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

